<template>
    <div class="list-wrapper">
        <yd-navbar  title="Apartments">

            <router-link to="/" slot="left">
                <yd-navbar-back-icon>Home</yd-navbar-back-icon>
            </router-link>

            <router-link to="/filter" slot="right">
                <span><p><img src="../../assets/icons/Filter.svg" alt=""></p></span>
            </router-link>

        </yd-navbar>

        <el-row :gutter="10">
  <el-col class="item" :xs="12" :sm="12" :md="6" :lg="6" :xl="6" v-for="(o, index) in rooms" :key="index">
    <el-card :body-style="{ padding: '10px' }">
      
      <div class="image" v-for="item,index in o.images.slice(0,1)" :key='index'>
                        <img :src='item.image' /> 
    </div>

      <div style="padding: 0 10px 10px 10px;">
        <span style="font-size:18px">{{o.compound}}</span>
		<p>{{o.desc}}</p>
        <div class="bottom clearfix">
          <time class="time">Add time{{o.add_time}} </time>
          <time class="time">Price：{{o.price}} </time>
         <yd-button type="hollow">Add to list</yd-button>
        </div>
      </div>
    </el-card>
  </el-col>
</el-row>


    </div>
</template>
<script>
import { APIGetroomlist } from '../../api/api'
import {mapGetters,mapState} from 'vuex'
import Vfilter from './filter'
export default {
    components:{
    Vfilter
    },
    data() {
        return {
            rooms: [],
            pic:'',
            
        }
    },
    computed:{
        ...mapState(['filters']),

    },
    methods:{
        popit(){
            this.$store.commit('UPDATEPOP')
        },
        getFilteredrooms() {
            this.filters
            APIGetroomlist(
               this.filters
            ).then( res => { 
                // console.log(res.data)
                this.rooms = res.data.results
                
            }).catch( err => { console.log( err )})
        }
    },
    created () {
        this.getFilteredrooms()
    }
    
}
</script>
<style lang="stylus" scoped>
.list-wrapper
    .item
        .image
            img
                width 100%


</style>






